import React, { useState } from 'react';
import '../App.css';
import {search} from "../services/searchService";


function Search() {
 
  const [searchQuery, setSearch] = useState("");
  const [searchResults, setResults] = useState("");
  
  const handleInput = event => {
    setSearch(event.target.value);
  };

  const handleSubmit = async() => {
    console.log(searchQuery);
    const search_query = {searchQuery};
    await search(search_query).then((response)=>{
      setResults(response)
      console.log(searchResults)
    });
  }


    return (
      <div>
        <input type="text" className="input" onChange = {handleInput} placeholder="Search..."/> 
        <button onClick = {handleSubmit} type = "submit" id="myInput">Submit</button>
        <div>{searchResults.searchQuery && searchResults.searchQuery.map(result => {
          return <h1 key={result}> {result} </h1>
        })}</div>
      </div>
    );
  }

export default Search;

