import React from 'react';
import '../App.css';
import {Link} from 'react-router-dom'

const style = {
  color: 'black'
}


function Navbar() {
  return (
    <nav>
        <h3>Logo</h3>
        <ul className="Nav-Links">
          <Link style = {style} to='/'>
            <li>Home</li>
          </Link>
          <Link style = {style} to='/search'>
            <li>Search</li>
          </Link>
        </ul>
    </nav>
  );
}

export default Navbar;


